  font-weight: var(--fw-400);
  line-height: var(--line-height-normal);
  font-size: var(--fs-1);
  padding-block: var(--space-2);
  cursor: pointer;
  color: var(--very-dark-saturated-blue);
  inline-size: 100%;
  outline: none;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

